.App {
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Nothing You Could Do", cursive;
  font-weight: 100;
  /* Pair maybe with: Montserrat */
}

body {
  background-color: #252121;
}

.segment {
  min-height: 100vh;
  box-sizing: border-box;
  /* no support for box-sizing in IE7 or lower */
  padding-block: min(3%, 30px);
  padding-inline: min(5%, 50px);
}

h1 {
  font-size: clamp(64px, 15vw, 110px);
}

h2 {
  /* font-size: clamp(28px, 6vw, 38px); */
  font-size: clamp(28px, 3.5vw, 38px);
  color: #e8b531;
}

.black-text {
  color: black;
}

.order-number {
  /* consider switching to left on mobile */
  text-align: right;
}

.landing-page {
  background-color: #252121;
  color: white;
  padding: 0;
  background-image: url("./images/landing-page-bg.jpg");
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: right;
}

.landing-page-overlay {
  background-image: linear-gradient(135deg, #252121, #252121, transparent);
}

.menu-page {
  background-color: #e8b531;
  background-image: url("./images/menu-bg.jpg");
  background-size: cover;
  color: black;
  padding: 0;
}

.menu-page-overlay {
  background-color: rgba(185, 143, 36, 0.63);
}

.info-page {
  background-color: #252121;
  color: #e8b531;
}
